.Carousel {

  &__list {
    display: flex;
    list-style: none;
  }

  &__item {
    position: relative;
    flex-shrink: 0;
    flex-grow: 0;

    &::after {
      content: "";
      display: block;
      padding: 0 0 100%;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
